import React, { useState } from "react";
import styled from "styled-components";

const Contact = () => {
    const [description, setDescription] = useState("")
    const [username, setUserName] = useState("")
    const [name, setName] = useState("")
    const [color, setColor] = useState("bg-yellow-400 items-center hover:bg-yellow-400 text-center flex justify-center font-bold")
    const [formulario, setFormulario] = useState("grid grid-flow-col grid-cols-1 grid-rows-1 gap-4")
    const [thanks, setThanks] = useState("hidden")
    const [boton, setBoton] = useState("flex w-full justify mt-10")
    const [label, setLabel] = useState("SEND")

    const send = () => {
        if (description && username && name) {
            const content = {
                "content": "Hi, this is a review",
                "embeds": [{
                    "title": name,
                    "description": description,
                    "footer": {
                        "text": "From: " + username
                    }
                }]
            }
        

        fetch('https://discord.com/api/webhooks/914535074492465162/ZFduLMgRsN9Ykd07awQtr5qRGqKZz99CkZPJLmgvtjAKIAkYY5q_T0FNpz-WPaaZqsWm', {
            method: 'POST',
            body: JSON.stringify(content),
            headers: {
                'Content-Type': 'application/json'
            }
        })
        .then(res => {
            setDescription("")
            setUserName("")
            setName("")
            setFormulario("hidden grid grid-flow-col grid-cols-3 grid-rows-1 gap-4")
            setThanks("grid grid-flow-col grid-cols-3 grid-rows-1 gap-4")
            setBoton("hidden")
        })

    }
    else{
        setColor("text-yellow items-center bg-red-500 hover:bg-red-400 text-center flex justify-center font-bold")
        setLabel("Error try again")
    }

    }
    return (
        <div className="h-screen bg-black flex flex-col justify-center p-60">
            <div className="text-center h-auto">
                <div className="mb-5 text-white text-2xl font-semibold">Leave A Feedback!</div>
                <div className={formulario}>
                <div className="flex flex-col">
                <div className="flex flex-col">
                    <label className="text-white text-lg font-semibold">Invite to your server</label>
                    <textarea className="w-full h-32 p-2 bg-gray-800 text-white" value={name} onChange={(e) => setName(e.target.value)} type="text" placeholder="discord.gg/someserver" className="p-4 rounded" rows="3"/>
                    <label className="text-white text-lg font-semibold">Description</label>
                    <textarea className="w-full h-32 p-2 bg-gray-800 text-white" value={description} onChange={(e) => setDescription(e.target.value)} type="text" placeholder="Your feedback here" className="p-4 rounded" rows="3"/>
                    <label className="text-white text-lg font-semibold">UserName</label>
                    <textarea className="w-full h-32 p-2 bg-gray-800 text-white" value={username} onChange={(e) => setUserName(e.target.value)} type="text" placeholder="Supercooluser#6969" className="p-4 rounded" rows="3"/>
                </div>
                </div>
                </div>
                <div className={thanks}>
                    <div></div>
                    <div className="text-center text-4xl font-bold  text-red-100 mt-10 mb-10">THANK YOU!</div>
                    <div></div>
                </div>
                </div>
                <div className={boton}>
                    <Boton onClick={send} className={color}>{label}</Boton>
                </div>
        </div>
    );
};

export default Contact;

const Boton = styled.button`
    padding: 5px;
    height: 60px;
    width: 150px;
    border-radius: 50px;
    `;